/*--------------------------------------------------------------
# Term Styles
--------------------------------------------------------------*/
.attachment-child .image-delete-button {
  display: none;
}
.attachment-child .image-delete-button.image-show {
  display: block;
}
.image-fields-wrap .attachment-child {
  display: inline-block;
  vertical-align: middle;
  width: 100px;
  height: 100px;
  overflow: hidden;
  position: relative;
}
.image-fields-wrap img {
  max-width: 100%;
}
.image-uploader {
  border: 2px dashed #b9b9b9;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.image-uploader:hover,
.image-uploader:focus {
  background: #faf8f5;
}
.image-uploader .image-upload-button {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 100px;
  height: 100px;
  border: 0;
  background: transparent;
  color: #000;
  outline: 0;
}
.image-uploader .image-upload-button .twp-icon-large {
  color: #6b6b6b;
  font-size: 34px;
  width: 50px;
  height: 50px;
  line-height: 50px;
}
.thumbnail-image .image-delete-button {
  position: absolute;
  top: 5px;
  right: 5px;
  background: #ffffffc2;
  color: black;
  border: solid 1px white;
}